<template>
  <div class="main">
    <!--    <input type="file" @change="change" id="upload" />-->
    <div class="up">
      <!--      显示修改后的图片-->
      <img
        v-show="show_image"
        src="style_image/default.jpg"
        class="show_image"
        id="show_image"
        @click="upload"
      />

      <img id="hide_image" src="style_image/default.jpg" class="hide_image" />

      <canvas v-show="!show_image" id="mix" class="show_image"></canvas>
    </div>
    <div class="down">
      <!--      风格图片-->
      <!--      <div class="icons" v-show="!show_image">-->
      <div class="icons">
        <img @click="download" class="icon" src="../assets/下载.png" />
        <img @click="download" class="icon" src="../assets/qq.png" />
        <img @click="download" class="icon" src="../assets/空间.png" />
        <img @click="download" class="icon" src="../assets/微信.png" />
        <img @click="download" class="icon" src="../assets/朋友圈.png" />
      </div>
      <div class="images">
        <img
          class="style_image"
          v-for="(model, key) in models"
          :src="'style_image/' + model.name + '.jpg'"
          :key="key"
          @click="transfer(model)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import * as tf from "@tensorflow/tfjs";

function exportCanvasAsPNG(id, fileName) {
  let canvasElement = document.getElementById(id);
  let MIME_TYPE = "image/png";
  let imgURL = canvasElement.toDataURL(MIME_TYPE);
  let link = document.createElement("a");
  link.href = imgURL;
  link.download = fileName;
  link.click();
}

export default {
  name: "Display",
  data() {
    return {
      show_image: true,
      tensor: null,
      file_name: "default",
      models: [
        {
          name: "default",
          model: null
        },
        {
          name: "starry",
          model: null
        },
        {
          name: "feathers",
          model: null
        },
        {
          name: "candy",
          model: null
        }
      ]
    };
  },
  methods: {
    upload() {
      let input = document.createElement("input");
      input.setAttribute("type", "file");
      input.setAttribute("accept", "image/*");
      console.log(input);
      input.click();
      input.addEventListener("change", () => {
        let img = document.getElementById("hide_image");
        let file = input.files[0];
        // console.log("aaa", input.files);
        console.log(file);
        this.file_name = file.name.split(".")[0];
        img.src = window.URL.createObjectURL(file);
        let show_image = document.getElementById("show_image");
        show_image.src = window.URL.createObjectURL(file);
        // 图像挂载成功之后再重新读取tensor
        img.onload = () => {
          let canvas = document.getElementById("mix");
          canvas.width = img.width;
          canvas.height = img.height;
          this.tensor = tf.browser.fromPixels(img);
        };
      });
    },
    change(e) {
      console.log(e);
      let img = document.getElementById("hide_image");
      let file = document.getElementById("upload").files[0];
      console.log(file);
      let url = window.URL.createObjectURL(file);
      img.src = url;
      let show_image = document.getElementById("show_image");
      show_image.src = url;
    },
    download() {
      console.log("download");
      exportCanvasAsPNG("mix", this.file_name + "_style.jpg");
    },
    async transfer({ model }) {
      console.log(model);
      let tensor = this.tensor;
      console.log(tensor.shape);
      let canvas = document.getElementById("mix");
      if (!model) {
        console.log("原图");
        this.show_image = true;
        await tf.browser.toPixels(tensor.toInt(), canvas);
      } else {
        this.show_image = false;
        console.log("转换");
        tensor = tensor.toFloat();
        console.log(tensor.shape);
        tensor = tensor.expandDims(0);
        console.log(tensor);
        let ret = model.execute(tensor);
        // let ret = model.predict(tensor);

        ret = ret.squeeze(0);
        console.log("ret", ret);
        console.log("max min", ret.max().dataSync(), ret.min().dataSync());
        ret = ret.clipByValue(0, 255).toInt();
        console.log("ret", ret);
        console.log("max min", ret.max().dataSync(), ret.min().dataSync());
        await tf.browser.toPixels(ret, canvas);
      }
    }
  },
  async mounted() {
    for (let model of this.models) {
      console.log(model);

      if (model.name !== "default") {
        const MODEL_DIR = "style_model/" + model.name + "/";
        const MODEL_URL = "tensorflowjs_model.pb";
        const WEIGHTS_URL = "weights_manifest.json";
        model.model = await tf.loadGraphModel(
          MODEL_DIR + MODEL_URL,
          MODEL_DIR + WEIGHTS_URL
        );
      }
      // model.model = await tf.loadGraphModel(model.model_path);
      console.log(model.model);
    }
    let img = document.getElementById("hide_image");
    this.tensor = tf.browser.fromPixels(img);
    console.log("mounted");
  }
};
</script>

<style scoped>
.main {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  justify-content: space-around;
  align-items: center;
}

.up {
  display: flex;
  width: 100%;
  height: 100%;
  /*max-width: 400px;*/
  /*max-height: 500px;*/
  justify-content: center;
  align-items: center;
  position: relative;
}

.icons {
  display: flex;
  width: 60%;
  justify-content: space-around;
  align-items: center;
  margin: 15px;
}

.icon {
  width: 32px;
  height: 32px;
  align-self: center;
  justify-self: center;
}

.down {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.images {
  display: flex;
  width: 100%;
  /*height: 100%;*/
  align-items: center;
  /*align-items: center;*/
  justify-content: space-around;
  /*justify-content: center;*/
  margin: 10px;
}

.style_image {
  width: 64px;
  height: 64px;
}

.hide_image {
  /*height: 90%;*/
  width: 85%;
  /*max-height: 450px;*/
  /*max-width: 90%;*/
  max-height: 85%;
  position: absolute;
  opacity: 0;
  z-index: 1;
  margin: 5px;
}

.show_image {
  width: 95%;
  /*height: 90%;*/
  /*width: auto;*/
  position: relative;
  /*max-width: 98%;*/
  /*max-height: 600px;*/
  max-height: 90%;
  margin: 5px;
  z-index: 2;
}
</style>
